*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --bg-color: #f8f9fa;
  --primary-color: #3b5bdb;
  --color-deep: #102591;
  --white-color: #fff;
  --text-color: #343a40;
  --text-color-Secondary: #868e96;
}

li {
  list-style: none;
}

button {
  border: none;
  background-color: transparent;
  font: inherit;
  outline: none;
  cursor: pointer;
}

label::first-letter,
button::first-letter {
  text-transform: uppercase;
}

body {
  font-family: "Poppins", sans-serif;
  background-color: var(--bg-color);
  color: var(--text-color);
  min-height: 100vh;
  display: grid;
  place-items: center;
}

.quiz {
  position: relative;
  width: 600px;
  min-height: 550px;
  background-color: var(--white-color);
  box-shadow: 0 0 15px 2px #dee2e6;
  border-radius: 12px;
  overflow: hidden;
}

.question-container {
  width: 100%;
  display: none;
  justify-items: center;
  padding-block: 48px;
}

.active {
  display: grid;
}

.question-box {
  text-align: center;
  margin-block-end: 48px;
  width: 100%;
}

.question-box .question {
  font-weight: 500;
  font-size: 24px;
  line-height: 1.3;
  width: 80%;
  margin: auto;
  line-height: 1.3;
  margin-block-end: 4px;
}

.question-box .text {
  color: var(--text-color-Secondary);
  font-size: 12px;
}

.answers {
  display: grid;
  gap: 24px;
}

.answer {
  display: flex;
  gap: 12px;
}

.answer label {
  font-weight: 400;
  cursor: pointer;
}

.explore {
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  background-color: var(--primary-color);
  color: var(--white-color);
  padding-block: 32px;
  font-size: 18px;
  font-weight: 500;
  transition: 0.2s;
}

.explore:is(:hover, :focus-within) {
  --primary-color: #364fc7;
}

.timer {
  position: absolute;
  top: 20px;
  right: 20px;
  font-size: 14px;
  font-weight: 600;
}

.move-btn {
  position: absolute;
  left: 5px;
  top: 5px;
  display: flex;
  gap: 12px;
}

.move-btn button {
  background-color: #748ffc;
  color: var(--white-color);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  display: block;
  width: 24px;
  height: 24px;
  padding-top: 2px;
}

.icon {
  width: 20px;
  height: 20px;
  stroke: #343a40;
}
